<template>
  <div class="productRanking">
    <van-row class="sub-header">
      <van-col class="text-left" span="18">
        <span class="info-left">{{ baseInfo.defaultText }}</span>
        <span class="info-left">{{ showPart.label }}</span>
        <span class="btn-left btn-solid" @click="handleLeftBtn">{{ baseInfo.leftBtn }}</span>
      </van-col>
      <van-col class="text-right" span="6">
        <span class="btn-right" @click="handleRightBtn">{{ baseInfo.rightBtn }}</span>
      </van-col>
    </van-row>
    <van-row style="height: 5px;"></van-row>
    <title-info :title="showPart.title" :remark="showPart.remark"></title-info>
    <van-popup v-model="showChoosePanel" lazy-render>
      <div>
        <div @click="activeItem(defaultPart, 99)">{{ defaultPart.label }}</div>
        <van-collapse v-model="activeNames" accordion>
          <van-collapse-item
          v-for="(item, index) of chooseList"
          :key="index"
          :title="item.parentLabel"
          :name="index + 1">
            <div
            v-for="(subItem, index) of item.subChooseList"
            :key="index"
            @click="activeItem(subItem, index)">{{ subItem.label }}</div>
          </van-collapse-item>
        </van-collapse>
      </div>
    </van-popup>
  </div>
</template>

<script>
import { NON_DYNAMIC_HEIGHT, PROMPT } from '@/utils'
import TitleInfo from '@/components/BaseTitleInfo'

export default {
  name: 'ProductRanking',
  components: { TitleInfo },
  data () {
    return {
      PROMPT, // 提示信息合辑
      baseInfo: { // 界面基础设置
        defaultText: '当前报表：', // 选择提示信息
        leftBtn: '点击选择', // 左上方选择按钮
        rightBtn: '导航4_0' // 右上方按钮（单个）
      },
      showPart: { // 需渲染数据
        title: '', // 标题
        remark: [], // 备注
        tableData: [] // 表格数据
      },
      active: 0, // 选中状态
      activeNames: '1',
      freshTime: '', // 备注-更新时间
      showChoosePanel: false, // 显示选择面板
      // chooseList: [{
      //   label: '数据源1(默认)', // 标签名
      //   title: '标题：默认页面', // 标题
      //   remark: ['更新时间：'], // 备注
      //   valInx: 0, // 含变量备注的位置（行数-1）
      //   tableData: [] // 表格数据
      // }],
      defaultPart: { // 需渲染数据
        label: '默认', // 小类名
        title: '标题：默认页面', // 标题
        remark: ['更新时间：'], // 备注
        valInx: 0, // 含变量备注的位置（行数-1）
        tableData: [] // 表格数据
      },
      chooseList: [{
        parentLabel: '类别1', // 大类名
        subChooseList: [{
          label: '类别1_1', // 小类名
          title: '标题： 类别1_1', // 标题
          remark: ['更新时间：'], // 备注
          valInx: 0, // 含变量备注的位置（行数-1）
          tableData: [] // 表格数据
        }]
      }, {
        parentLabel: '类别2',
        subChooseList: [{
          label: '类别2_1'
        }, {
          label: '类别2_2'
        }, {
          label: '类别2_3'
        }]
      }]
    }
  },
  computed: {
    height () { // 表格高度 = 非动态高度 - 备注高度 - 本组件的占位高度
      return NON_DYNAMIC_HEIGHT - 18 * this.remark.length - 5
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      this.showPart = this.defaultPart
      this.showPart.remark[0] = '更新时间： ' + new Date().toLocaleString()
    },
    handleLeftBtn () { // 点击左上方按钮，进行报表选择（按钮操作与当前页面数据对应）
      this.showChoosePanel = true
    },
    handleRightBtn () { // 点击右上方按钮（按钮操作与当前页面数据对应）
      alert(this.baseInfo.rightBtn)
    },
    activeItem (item, index) {
      this.baseInfo.rightBtn = '导航4_' + index
      this.freshTime = Math.random()
      this.active = index // 标记选中状态
      this.showPart = item // 替换需显示数据
      this.showPart.remark[item.valInx] = '更新时间： ' + this.freshTime // 处理备注数据
      this.showChoosePanel = false
    }
  }
}
</script>

<style>
.btn-solid {
  background-color: orange;
  border: 1px solid orange;
  border-radius: 5px;
  padding: 1px 6px;
}
</style>



